/*==========STYLES===========*/
.swiper-blog-home {
  overflow: hidden;
  background-color: #B075D6;
} 

  .contentBlog {
    display: flex;
    align-items: center;
  }
  .content--blog-item {
    display: flex;
    align-items: center;
  }
  .content--blog-item .feedback{
    padding-left: 80px;
    padding-right: 70px;
    position: relative;
    flex: 1 1;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    align-content: center;
  }

  .content--blog-item .feedback p{
    line-height: 27px;
    color: #fff;
  }


/*==========ANIMATION===========*/
/*==========RESPONSIVE===========*/
/*Mobil*/
@media screen and (max-width:512px){
  .content--blog-item .feedback {
    padding: 30px 20px;
    height: 390px;
  }
  .blog_home_area h2 {
    font-size: 25px !important;
  }
}
/*Tablet*/
@media (min-width: 513px) and (max-width:821px){
  .content--blog-item .feedback {
    padding: 30px 20px;
}
}
